h4 {
  margin: 10px 0;
}
.container {
  padding: 40px 0;
}
.align-left {
  float: left;
}
.align-right {
  float: right;
}
.stats {
  margin: 15px 0;
}
.stats > span {
  font-weight: bold;
  display: block;
  margin-bottom: 10px;
}

.user-info img {
  max-width: 200px;
  max-height: 200px;
}
.user-info a {
  margin: 10px 0 0 0;
  display: block;
  padding: 10px 0;
  font-size: 24px;
  font-weight: bold;
}
.card {
  border: none;
}
.card .card-body {
  padding-top: 0;
}
.info textarea {
  height: 100px;
}

.username {
  margin: 15px 0;
}
.edit-profile img {
  max-width: 320px;
}

.tabs a.active {
  color: black;
}
.user-card {
  display: -ms-flexbox;
  display: flex;
}
.user-card img {
  max-width: 65px;
  max-height: 65px;
  margin-bottom: 10px;
}
.user-card .info {
  padding-left: 10px;
}
.user-card .username {
  padding-right: 10px;
}
.user-card .link {
  padding: 10px 0 0 0;
}
.tabs {
  padding-bottom: 20px;
}
.tabs span {
  padding-right: 20px;
}


